
body {
  background: #eee;
  margin: 0;
}
#app {
  width: 800px;
  height: 800px;
  position: relative;
}
.editor{
  top: 0;
  left: 0;
  width: 800px;
  height: 800px;
  position: absolute;
}
.editor__frame{
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 300px;
  height: 300px;
}
.editor__textarea{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  height: 100%;
  padding: 0.5rem;
  border: 1px solid blue;
}




