
svg {
  -ms-touch-action: none;
      touch-action: none;
  background: #FCC;
  position: absolute;
  /*opacity: 0.9;*/
}
.hint {
  width: 1px;
  height: 100px;
  background: red;
  position: absolute;
  z-index: -100;
}
.move {
  fill: gray;
}
.hover {
  fill: yellow;
}
.resize-hover {
  fill: blue;
}
.item {
  fill: white;
  stroke: black;
}
.item:hover {
  opacity: 0.5;
}



